@import '../../styles/mixins.scss'; 

.contacts__container{
    @include container;
}

.contacts__container input{
    @include input;    
}

.contacts__link{
    @include link;
}

.contacts__table, tr, td{
   @include table;
}


.contacts__table a{   
    text-decoration: none;   
}

.contacts__sort{
    display: flex;
    align-items: center;    
}

.contacts__sort p{
    color: $color-font;
    margin-right: 5px;
}


@media(max-height:900px){
    .contacts__table, th, tr, td{
        padding: 5px !important;
        font-size: 14px !important;
     }

     .contacts__container input{
        padding: 5px 10px !important;   
    }
     
} 

