@import '../../styles/variables.scss'; 

.footer{
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100px; 
    color: $color-font;   
    background: url('../../assets/cover-image.jpg') no-repeat center 94.5%;  
}


@media(max-height:900px){
    .footer{
        background: url('../../assets/cover-image.jpg') no-repeat center 93%; 
        height: 60px;
    }
}